<template>
  <div class="home">
    <the-navbar />
    <main class="home main mt-5 pt-5">
      <div class="container">
        <div class="row">
          <div class="col-12">
            <Skyler hidden />
            <div class="row">
              <div class="col-12 col-sm-9">
                <img
                  src="https://res.cloudinary.com/dbszizqh4/image/upload/v1562472720/default.jpg"
                  alt="imagen"
                  class="img-home mb-3"
                />
              </div>
              <div class="col-12 col-sm-3">
                <div class="row">
                  <div class="col-12">
                    <Adds />
                  </div>
                  <div class="col-12">
                    <Adds />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-12">
            <div class="row">
              <div
                class="col-12 col-sm-4 col-lg-3"
                v-for="(item, index) in allPages"
                :key="index"
              >
                <Site :image="item.image" :name="item.name" :slug="item.slug" />
              </div>
            </div>
          </div>
          <div class="col-12 py-3">
            <the-footer />
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import { db } from "@/main";
import axios from "axios";

import Adds from "@/components/Adds";
import Site from "@/components/Site";
import Skyler from "@/components/Skyler";
import TheFooter from "@/components/TheFooter";
import TheNavbar from "@/components/TheNavbar";

export default {
  name: "Home",
  components: {
    Adds,
    Site,
    Skyler,
    TheFooter,
    TheNavbar
  },
  data() {
    return {
      allPages: ""
    };
  },
  created() {
    this.getAllPage();
  },
  methods: {
    async getAllPage() {
      await axios(`${db}/pages`)
        .then(result => {
          this.allPages = result.data;
        })
        .catch(error => {
          var errorCode = error.code;
          var errorMessage = error.message;
          if (errorCode == "auth/weak-password") {
            alert("The password is too weak");
          } else {
            alert(errorMessage);
          }
          console.log(error);
        });
    }
  }
};
</script>

<style scoped>
.img-home {
  width: 100%;
  height: 234px;
  object-fit: cover;
  position: center center;
}

.card-img-top {
  height: 208px;
  object-fit: cover;
  position: center center;
}
</style>
